import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, EventoMHO } from '../../../../models/dtos/mho/eventoMHO';
import { TIPO_EVENTO_MHO_ENUM_VALUES } from '../../../../models/enumerated/mho/tipoEventoMHOEnum';

interface EventoMHOEditViewProps extends EditViewProps<EventoMHO> {}

const EventoMHOEditView: FC<EventoMHOEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<EventoMHO>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Eventos" wikiId={2029}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 8 } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              placeholder="Informe o Código do Evento."
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
              type={InputType.NUMBER}
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 200, required: true } }}
              id="descricao"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipo"
              isPrependLabel={true}
              label="Tipo"
              placeholder="Selecione o tipo de evento."
              inputDropDownProps={{
                optionsProvider: TIPO_EVENTO_MHO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default EventoMHOEditView;
